import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {variable} from '../global/url';

export default function InventoryDetail({match}) {
    const [inventory, setInventory] = useState({})
    const id = match.params.id

    useEffect(() => {
        axios({
            method: 'GET',
            url: variable.MainUrl + `api/v1/inventory/${id}`
        }).then(response => {
            setInventory(response.data)
        })
    }, [id])

    return(
        <div className="container">
        <h3 className="text-center">InventoryDetail</h3>
          <Card border="dark">
            <Card.Header><span className="text-muted">Serial Number: </span>{inventory.serial_number}</Card.Header>
            <Card.Body>
              <Card.Title>
                <p><span className="text-muted">Title: </span>{inventory.title}</p>
                <p><span className="text-muted">Category: </span><Link to={{ pathname: `/category/${inventory.category}`, fromDashboard: false }}>{inventory.category}</Link></p>
                <p><span className="text-muted">Owner: </span><Link to={{ pathname: `/owner/${inventory.owner}`, fromDashboard: false }}>{inventory.owner}</Link></p>
                <p><span className="text-muted">Price: </span>{inventory.price}$</p>
                <p><span className="text-muted">Date of purchase: </span>{inventory.date_purchase}</p>
              </Card.Title>
              <Card.Text>
                {inventory.inventory_description}
              </Card.Text>
            </Card.Body>
          </Card>
        </div>
    )
}
