import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Table } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { variable } from '../global/url';


export default function InventoryTable() {

    const [inventory, setInventory] = useState([])
    let num = 1

    useEffect(() => {
        axios({
            method: "GET",
            url: variable.MainUrl + "api/v1/inventory/",
        }).then(response => {
            setInventory(response.data)
        })
    }, [])

    return(
        <div className="container">
        <h3 className="text-center">All Inventory</h3>
        <Table striped bordered hover variant="light" className="text-center">
          <thead>
            <tr>
              <th>№</th>
              <th>Serial</th>
              <th>Category</th>
              <th>Title</th>
              <th>Description</th>
              <th>Owner</th>
            </tr>
          </thead>
          <tbody>

          </tbody>
        </Table>
            <div className="text-center">
                <Link to={{ pathname: '/inventory_add', fromDashboard: false }}><button className="btn btn-success">Add Inventory</button></Link>
            </div>
        </div>
    )
}
