import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Table } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { variable } from '../global/url';


export default function OwnerTable() {

    const [owner, setOwner] = useState([])
    let num = 1

    useEffect(() => {
        axios({
            method: "GET",
            url: variable.MainUrl + "api/v1/owner/",
        }).then(response => {
            setOwner(response.data)
        })
    }, [])

    return(
        <div className="container">
        <h3 className="text-center">All Category</h3>
        <Table striped bordered hover variant="light" className="text-center">
          <thead>
            <tr>
              <th>№</th>
              <th>Name</th>
              <th>Description</th>
              <th>Category</th>
            </tr>
          </thead>
          <tbody>
            {owner.map(ow => (
            <tr key={ow.id}>
              <td className="col-1">{num++}</td>
              <td className="col-4 text-start"><Link to={{ pathname: `/owner_detail/${ow.id}`, fromDashboard: false }} className="ms-2">{ow.surname} {ow.name[0]}.</Link></td>
              <td className="col-5">{ow.owner_description}</td>
              <td className="col-2">{ow.category}</td>
            </tr>
            ))}
          </tbody>
        </Table>
            <br/>
            <div className="text-center">
                <Link to={{ pathname: '/owner_add', fromDashboard: false }}><button className="btn btn-success">Add Owner</button></Link>
            </div>
        </div>
    )
}
