import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Table } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { variable } from '../global/url';


export default function CategoryTable() {

    const [category, setCategory] = useState([])
    let num = 1

    useEffect(() => {
        axios({
            method: "GET",
            url: variable.MainUrl + "api/v1/category/",
        }).then(response => {
            setCategory(response.data)
        })
    }, [])

    return(
        <div className="container">
        <h3 className="text-center">All Category</h3>
        <Table striped bordered hover variant="light" className="text-center">
          <thead>
            <tr>
              <th>№</th>
              <th>Title</th>
              <th>Description</th>
            </tr>
          </thead>
          <tbody>
            {category.map(cat => (
            <tr key={cat.id}>
              <td className="col-1">{num++}</td>
              <td className="col-4 text-start"><Link to={{ pathname: `/category_detail/${cat.id}`, fromDashboard: false }} className="ms-2">{cat.category_title}</Link></td>
              <td className="col-7">{cat.category_description}</td>
            </tr>
            ))}
          </tbody>
        </Table>
            <br/>
            <div className="text-center">
                <Link to={{ pathname: '/category_add', fromDashboard: false }}><button className="btn btn-success">Add Category</button></Link>
            </div>
        </div>
    )
}
